/* eslint import/no-extraneous-dependencies: 0 */
import { definition } from '@eui/component';
import { Panel } from '@eui/panel';
import { html, nothing, classMap } from '@eui/lit-component';
import '@eui/base';
import style from './userSettingsPanel.css';

@definition('eui-user-settings-panel', { style })
export default class SettingsPanel extends Panel {
  /**
   * @public
   * @function constructor
   * @description The constructor for the component, set options and bind events
   */
  constructor() {
    super();
    this._handleLogout = this._handleLogout.bind(this);
    this._handleThemeToggle = this._handleThemeToggle.bind(this);
    this._handleLocaleChange = this._handleLocaleChange.bind(this);
  }

  /**
   * @private
   * @function _getUserName
   * @description Component did connect callback
   */
  _getUserName() {
    const storage = window.localStorage;
    const username = storage.getItem('username') || 'ejoesoap';
    return username;
  }

  /**
   * @private
   * @function didConnect
   * @description Component did connect callback
   */
  didConnect() {
    // Connect this application to the appStore to observe for state
    // changes for a specified state property or list of properties
    this.state = this.storeConnect(['locale', 'theme']);
  }

  /**
   * @private
   * @function _handleThemeToggle
   * @description Handles dispatching the switch theme event
   */
  _handleThemeToggle(e) {
    const theme = e.detail.on ? 'dark' : 'light';
    this.dispatch('SWITCH_THEME', theme);
  }

  /**
   * @private
   * @function _handleLogout
   * @description Handles the logout action from the button
   */
  _handleLogout() {
    // Dispatch an action to the store to update the state and execute any
    // pre state change functionality
    this.dispatch('ACTION_LOGOUT', { isLoggedIn: false });

    // Execute a method on the authentication plugin to clear the user
    // session, however that may have been stored
    this.plugin('authentication', 'clearSession');
  }

  /**
   * @private
   * @function _handleLocaleChange
   * @description Handles the locale change event
   */
  _handleLocaleChange(event) {
    this.dispatch('UPDATE_LOCALE', { localeId: event.target.value[0].value });
  }

  render() {
    const { loc, localeIds } = window.EUI.Localizer;
    const {
      MY_SETTINGS,
      SWITCH_THEME,
      SIGN_OUT,
      SWITCH_LANGUAGE,
      LIGHT,
      DARK,
      LANGUAGE,
    } = loc;

    return html`
    <link rel="stylesheet" href=${window.containerConfig.sharedCSS}>
    <div class="settings-panel">
      <div class="content">
        <div class="column sm-12 container">
          <div class="profile">
            <eui-v0-icon name="profile" size="44px"></eui-v0-icon>
            <div class="username">${this._getUserName()}</div>
          </div>
          <div class="content">
            <div class="title">${MY_SETTINGS || 'My Settings'}</div>
            <div class="item">
              <div class="left">${SWITCH_THEME || 'Switch Theme'}</div>
              <div class="right">
                <eui-base-v0-switch
                  class=${classMap({ align__right: localeIds.length <= 1 })}
                  label-on="${DARK || 'Dark'}"
                  label-off="${LIGHT || 'Light'}"
                  ?on=${this.state.theme === 'dark'}
                  @eui-switch:change=${this._handleThemeToggle}></eui-base-v0-switch>
              </div>
            </div>
            ${localeIds.length > 1 ? html`
              <div class="item">
                <div class="left">${SWITCH_LANGUAGE || 'Switch Language'}</div>
                <div class="right">
                  <eui-base-v0-dropdown width="100%" id="locale-dropdown" label="${LANGUAGE || 'Choose language'}" @eui-dropdown:change=${this._handleLocaleChange} data-type="single">
                    ${localeIds.map(locale => html`
                      <eui-base-v0-menu-item
                        value="${locale}"
                        label="${window.EUI.Localizer.loc.languages[locale] || locale}"
                        ?selected=${locale === this.state.locale}
                      ></eui-base-v0-menu-item>`)}
                  </eui-base-v0-dropdown>
                </div>
              </div>
            ` : nothing}
          </div>
        </div>
        </div>
      <div class="footer">
        <eui-base-v0-button icon="logout" class="bottom" big fullwidth @click=${this._handleLogout}>${SIGN_OUT || 'Sign out'}</eui-base-v0-button>
      </div>
    </div>`;
  }
}
